.App {
  text-align: center;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-header {
  background-color: #0063b6;
  min-height: 60px;
  width: 100%;
  border-bottom: 1px solid #8db6d7;
}

.App-content {
  background-color: #f2f2f2;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}

.App-sidebar {
  background-color: #f9f9f9;
  height: 100%;
  min-width: 270px;
  max-width: 270px;
}

.App-main {
  background: -webkit-linear-gradient(left, #e3e3e3, #e5e5e5 2px);
  background: -o-linear-gradient(left, #e3e3e3, #e5e5e5 2px);
  background: linear-gradient(to right, #e3e3e3, #e5e5e5 2px);
  width: 100%;
}


